import './autres.css';

export default function Autres({ imageSrc }) {
  return (
    // <div className="imageContainer">
    //     <img className="img img-fluid" src={imageSrc} alt="" />
    // </div>
  <div className="responsive">
    <div className="gallery">
      <a rel='noreferrer' target="_blank" href={imageSrc}>
        <img style={{width: "100%"}} src={imageSrc} alt="Cinque Terre" width="600" height="400" />
      </a>
      <div className="desc">Add a description of the image here</div>
    </div>
  </div>
  )
}